<template>
    <div class="p-20">
        <div class="filter-container" style="margin-bottom: 20px">
            <el-input class="filter-item" style="width: 300px;"
                      placeholder="请输入会员姓名、UID、手机号码、邮箱"
                      @keyup.native.enter="handleFilter"
                      v-model="listQuery.nameUidNum">
            </el-input>

            <span>实名认证状态</span>
            <el-select clearable style="width: 110px" class="filter-item" v-model="listQuery.identityValidate">
                <el-option v-for="status in importanceOptions" :key="status.key" :label="status.display_name"
                           :value="status.key">
                </el-option>
            </el-select>

            <span>会员状态</span>
            <el-select clearable class="filter-item" style="width: 110px" v-model="listQuery.accountStatus">
                <el-option v-for="item in  calendarTypeOptions" :key="item.key" :label="item.display_name"
                           :value="item.key">
                </el-option>
            </el-select>
            <el-button class="filter-item" @click="handleFilter" size="small" style="margin-left: 30px">
                查询
            </el-button>
            <div style="margin-top: 12px">
                <el-button type="primary" class="filter-item" @click="handleDeleteEdit()" size="small">
                    详情
                </el-button>
                <el-button type="primary" class="filter-item" @click="enableDisable(1,'启用')" size="small"
                           style="margin-left: 6px">
                    启用
                </el-button>
                <el-button type="primary" class="filter-item" @click="enableDisable(2,'禁用')" size="small"
                           style="margin-left: 6px">
                    禁用
                </el-button>
                <el-button type="primary" class="filter-item" @click="operate(3,'重置手机')" size="small"
                           style="margin-left: 6px">
                    重置手机号
                </el-button>
                <el-button type="primary" class="filter-item" @click="operate(4,'重置邮箱')" size="small"
                           style="margin-left: 6px">
                    重置邮箱
                </el-button>
                <el-button type="primary" class="filter-item" @click="operate(1,'重置交易密码')" size="small"
                           style="margin-left: 6px">
                    重置交易密码
                </el-button>
                <el-button type="primary" class="filter-item" @click="operate(2,'重置Google')" size="small"
                           style="margin-left: 6px">
                    重置Google
                </el-button>
                <el-button type="primary" class="filter-item" @click="operate(5,'修改推荐人')" size="small"
                           style="margin-left: 6px">
                    修改推荐人
                </el-button>
                <el-button type="primary" class="filter-item" @click="operate(6,'修改用户等级')" size="small"
                           style="margin-left: 6px">
                    修改用户等级
                </el-button>
                <el-button type="primary" class="filter-item" @click="operate(7,'解锁登录密码')" size="small"
                           style="margin-left: 6px">
                    解锁登录密码
                </el-button>
            </div>
        </div>
        <div>
            <el-table
                    ref="singleTable"
                    :data="tableData"
                    v-loading="listLoading"
                    element-loading-text="给我一点时间"
                    :highlight-current-row="true"
                    @row-click="handleCurrentChange"
                    style="width: 100%;">
                <el-table-column
                        v-for="item in tableHeader"
                        :key="item.prop"
                        :label="item.label"
                        :prop="item.prop"
                        :width="item.width"
                        :min-width="item.minWidth"
                        :align="item.align"
                        :formatter="item.formatter"
                        show-overflow-tooltip>
                </el-table-column>

                <!-- <template scope="scope" v-if=" item.prop == 'accountStatus' ">
                  <span v-if=" item.prop == 'accountStatus' " :class="{ colorRed : scope.row.accountStatus == 2 }">
                    {{ scope.row.accountStatus === 1 ? '正常' : (value === 2 ? '禁用' : '未知') }}
                  </span>
                  <span >
                  </span>
                </template> -->

                <el-table-column
                        :key="'状态'"
                        :label="'状态'"
                        :prop="'accountStatus'"
                        :width="80"
                        :min-width="80"
                        :align="'center'"
                        show-overflow-tooltip>

                    <template scope="scope">
                    <span :class="{ colorRed : scope.row.accountStatus == 2 , colorBlue : scope.row.accountStatus > 2 }">
                      {{ scope.row.accountStatus === 1 ? '正常' : ( scope.row.accountStatus === 2 ? '禁用' : '未知') }}
                    </span>
                    </template>

                </el-table-column>

                <el-table-column
                        v-for="item in tableHeader2"
                        :key="item.prop"
                        :label="item.label"
                        :prop="item.prop"
                        :width="item.width"
                        :min-width="item.minWidth"
                        :align="item.align"
                        :formatter="item.formatter"
                        show-overflow-tooltip>
                </el-table-column>


                <!--<el-table-column
                        fixed="right"
                        label="操作"
                        width="520">
                    <template scope="scope" class="filter-operation">
                        <ul style="list-style: none;padding: 0px;height: 20px;margin: 0px">
                            <li style="float:right;width: 64px">
                                <el-button
                                        size="small"
                                        type="text"
                                        @click="operate(3,scope.row.userId,'重置手机')">重置手机
                                </el-button>
                            </li>
                            <li style="float:right;width: 80px">
                                <el-button
                                        size="small"
                                        type="text"
                                        @click="operate(1,scope.row.userId,'重置交易密码')">重置交易密码
                                </el-button>
                            </li>
                            <li style="float:right;width: 64px">
                                <el-button
                                        size="small"
                                        type="text"
                                        @click="operate(4,scope.row.userId,'重置邮箱')">重置邮箱
                                </el-button>
                            </li>
                            <li style="float:right;width: 80px">
                                <el-button
                                        size="small"
                                        type="text"
                                        @click="operate(2,scope.row.userId,'重置Google')">重置Google
                                </el-button>
                            </li>
                            <li style="float:right;width: 80px">
                                <el-button
                                        size="small"
                                        type="text"
                                        @click="operate(6,scope.row.userId,'修改用户等级')">修改用户等级
                                </el-button>
                            </li>
                            <li style="float:right;width: 72px">
                                <el-button
                                        size="small"
                                        type="text"
                                        @click="operate(5,scope.row.userId,'修改推荐人')">修改推荐人
                                </el-button>
                            </li>
                            <li style="float:right;width: 32px"
                                v-if="scope.row.accountStatus===2">
                                <el-button
                                        size="small"
                                        type="text"
                                        @click="enableDisable(1,scope.row.userId,'启用')">启用
                                </el-button>
                            </li>
                            <li style="float:right;width: 32px"
                                v-else-if="scope.row.accountStatus===1">
                                <el-button
                                        size="small"
                                        type="text"
                                        @click="enableDisable(2,scope.row.userId,'禁用')">禁用
                                </el-button>
                            </li>
                            <li style="float:right;width: 32px" v-if="scope.row.identityValidate!==0">
                                <el-button type="text" size="small"
                                           @click="handleDeleteEdit(scope.row.userId,scope.row.identityValidate)">详情
                                </el-button>
                            </li>

                        </ul>
                    </template>
                </el-table-column>-->
            </el-table>
            <div v-show="!listLoading" class="pagination">
                <pages :total='total' :userPageSize='userPageSize' :userPage='userPage' :listQuery='listQuery'></pages>
            </div>
        </div>
        <el-dialog v-loading="listLoad" element-loading-text="给我一点时间" title="用户明细" :visible.sync="userDetails">
            <el-form :model="temp">
                <el-form-item label="UID" :label-width="formLabelWidth">
                    <template>
                        <div>
                            {{temp.userId}}
                        </div>
                    </template>
                </el-form-item>
                <el-form-item label="姓名" :label-width="formLabelWidth">
                    <template>
                        <div>
                            {{temp.name}}
                        </div>
                    </template>
                </el-form-item>
                <el-form-item label="国家/地区" :label-width="formLabelWidth">
                    <template>
                        <div v-for="item in citybos" v-if="item.cityId === temp.identityArea">
                            {{item.cityName}}
                        </div>
                    </template>
                </el-form-item>
                <el-form-item label="手机号码" :label-width="formLabelWidth">
                    <template>
                        <div>
                            {{temp.phoneNum}}
                        </div>
                    </template>
                </el-form-item>
                <el-form-item label="账号状态" :label-width="formLabelWidth">
                    <template>
                        <div v-if="temp.accountStatus === 1">
                            正常
                        </div>
                        <div v-else>
                            禁用
                        </div>
                    </template>
                </el-form-item>
                <el-form-item label="用户等级" :label-width="formLabelWidth">
                    <template>
                        {{temp.memberRank}}
                    </template>
                </el-form-item>
                <el-form-item label="注册时间" :label-width="formLabelWidth">
                    <template>
                        <div>
                            {{temp.createTime}}
                        </div>
                    </template>
                </el-form-item>
                <el-form-item label="是否绑定银行卡" :label-width="formLabelWidth">
                    <template>
                        <div v-if="temp.bankAmount >0">
                            已绑定({{temp.bankAmount}})
                        </div>
                        <div v-else>
                            未绑定
                        </div>
                    </template>
                </el-form-item>
                <el-form-item label="实名认证信息" :label-width="formLabelWidth">
                    <template>
                        <div v-for="item in importanceOptions" v-if="item.key === temp.identityValidateState">
                            {{item.display_name}}
                        </div>
                    </template>
                </el-form-item>
                <el-form-item label="证件认证信息" :label-width="formLabelWidth">
                    <template>
                        <div v-for="item in importanceOptions" v-if="item.key === temp.photoidValidate">
                            {{item.display_name}}
                        </div>
                    </template>
                </el-form-item>
                <el-form-item label="视频认证信息" :label-width="formLabelWidth">
                    <template>
                        <div v-for="item in importanceOptions" v-if="item.key === temp.videoValidate">
                            {{item.display_name}}
                        </div>
                    </template>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="userDetails=false">取 消</el-button>
                <el-button type="primary" @click="handleEdit">修改信息</el-button>
            </div>
        </el-dialog>


        <!------------------------------------------------下面为查看身份信息---------------------------------------------------------------------------->
        <el-dialog v-loading="listLoad" element-loading-text="给我一点时间" title="身份认证信息" :visible.sync="dialogFormVisible">
            <el-form :model="userbase">
                <el-form-item label="姓名" :label-width="formLabelWidth">
                    <el-input v-model="temp.name" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="国家/地区" :label-width="formLabelWidth">
                    <el-select class="filter-item" v-model="userbase.identityArea"
                               @change="handleChange">
                        <el-option v-for="item in citybos"
                                   :key="item.countryCode"
                                   :label="item.cityName"
                                   :value="item.cityId">
                        </el-option>
                    </el-select>
                </el-form-item>

                <el-form-item label="证件类型" :label-width="formLabelWidth">
                    <el-select class="filter-item" v-model="userbase.identityType">
                        <el-option v-for="item in identityType"
                                   :key="item.id"
                                   :label="item.name"
                                   :value="item.id">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="证件号码" :label-width="formLabelWidth">
                    <el-input v-model="userbase.idCard"></el-input>
                </el-form-item>

                <!----------------- 下面是身份认证审核代码------------------>
                <el-form-item label="身份认证审核" :label-width="formLabelWidth">
                    <div v-if="temp.identityValidateState===0">未提交</div>
                    <div v-else-if="temp.identityValidateState===3&&temp.identityValidate===3">审核通过</div>
                    <div v-else>
                        <el-radio v-model="userbase.identityValidate" :label="3">通过审核</el-radio>
                        <el-radio v-model="userbase.identityValidate" :label="4">驳回</el-radio>
                    </div>
                </el-form-item>
                <div v-if="userbase.identityValidate==4">
                    <el-form-item label="驳回原因" :label-width="formLabelWidth">
                        <el-input v-model="userbase.identityRemark" auto-complete="off"
                                  value="form.checkRemark"></el-input>
                    </el-form-item>
                </div>


                <div v-if="temp.identityValidateState===3&&temp.identityValidate===3">
                    <el-form-item label="认证照片认证" :label-width="formLabelWidth">
                        <div v-if="temp.photoidValidateState===0">未提交</div>
                        <div v-else-if="temp.photoidValidateState===4">已驳回</div>
                        <div v-else-if="temp.photoidValidateState==3&&temp.videoValidateState===3">审核通过</div>
                        <div v-else>
                            <el-radio v-model="userbase.photoidValidate" :label="3">通过审核</el-radio>
                            <el-radio v-model="userbase.photoidValidate" :label="4">驳回</el-radio>
                        </div>
                    </el-form-item>
                    <div v-if="temp.photoidValidateState!==0">
                        <el-form-item label="证件照片" :label-width="formLabelWidth">
                            <img v-bind:src="userbase.frontPathImg" class="images"><img
                                v-bind:src="userbase.reversePathImg"
                                class="images"> <img v-bind:src="userbase.handPath"
                                                     class="images">
                        </el-form-item>
                    </div>
                    <div v-if="temp.photoidValidate==4">
                        <el-form-item label="驳回原因" :label-width="formLabelWidth">
                            <el-input v-model="userbase.photoidRemark" auto-complete="off"
                                      value="form.checkRemark"></el-input>
                        </el-form-item>
                    </div>
                </div>


                <div v-if="temp.photoidValidate===3&&temp.photoidValidateState===3">
                    <el-form-item label="视频认证" :label-width="formLabelWidth">
                        <div v-if="temp.videoValidateState===4">已驳回</div>
                        <div v-if="temp.videoValidateState===0">未提交</div>
                        <div v-if="temp.videoValidateState===3">已经通过</div>
                        <div>
                            <el-radio :disabled="temp.videoValidateState===0" v-model="userbase.videoValidate" :label="3">通过审核</el-radio>
                            <el-radio :disabled="temp.videoValidateState===0" v-model="userbase.videoValidate" :label="4">驳回</el-radio>
                        </div>
                    </el-form-item>
                    <div v-if="userbase.videoValidate===4">
                        <el-form-item label="驳回原因" :label-width="formLabelWidth">
                            <el-input v-model="userbase.videoRemark" auto-complete="off"
                                      value="form.checkRemark"></el-input>
                        </el-form-item>
                    </div>
                </div>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible=false">取 消</el-button>
                <el-button type="primary" @click="updateUser">确 定</el-button>
            </div>
        </el-dialog>


        <el-dialog title="修改信息" :visible.sync="modifyReferrerVisible">
            <el-form :model="resetOrUpdateData">
                <el-form-item label="推荐人" :label-width="formLabelWidth">
                    <el-input v-model="resetOrUpdateData.referrerNum" auto-complete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="modifyReferrerVisible=false">取 消</el-button>
                <el-button type="primary" @click="resetOrUpdate(5)">确 定</el-button>
            </div>
        </el-dialog>

        <el-dialog title="修改信息" :visible.sync="modifyLevelVisible">
            <el-form :model="resetOrUpdateData">
                <el-form-item label="用户等级" :label-width="formLabelWidth">
                    <el-input-number v-model="resetOrUpdateData.level" auto-complete="off" :min="0"
                                     :max="7"></el-input-number>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="modifyLevelVisible=false">取 消</el-button>
                <el-button type="primary" @click="resetOrUpdate(6)">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    /*import fetch from '@/utils/fetch'*/
    import DCTP from '@/utils/dctp';
    import {
        fetchList,
        alterUser,
        disableUser,
        verifiedUser,
        selectUser,
        getCity,
        resetUpdate,
        unlockLoginPwd
    } from '@/api/management';
    import Pages from '@/components/page/page';

    //启用禁用筛选条件
    const calendarTypeOptions = [
        {key: '', display_name: '全部状态'},
        {key: '1', display_name: '启用'},
        {key: '2', display_name: '禁用'},
    ];
    const identityType = [
        {id: 0, name: '身份证'}, {id: 2, name: '护照'}
    ];
    // const pagenums = DCTP.pagenums;
    const logintypeOptions = [
        {key: 0, display_name: '全部状态'},
        {key: 1, display_name: '手机注册'},
        {key: 2, display_name: '邮箱注册'},
        {key: 3, display_name: 'QQ注册'},
        {key: 4, display_name: '微信注册'},
    ];
    //实名审核筛选条件
    const importanceOptions = [
        {key: '', display_name: '全部状态'},
        {key: 0, display_name: '未提交'},
        {key: 1, display_name: '待审核'},
        {key: 3, display_name: '已认证'},
        {key: 4, display_name: '不通过'},
    ];

    export default {
        components: {Pages},
        data() {
            return {
                logintypeOptions,
                //总条数
                total: null,
                //返回来列表的数据
                tableData: [],
                citybos: [],
                identityType,
                listLoading: true,
                listLoad: true,
                //查看身份新增框自动隐藏
                userDetails: false,
                dialogFormVisible: false,
                modifyReferrerVisible: false,
                modifyLevelVisible: false,
                userbase: {
                    //uid
                    userId: '',
                    //真实姓名
                    name: '',
                    //证件地区
                    identityArea: '',
                    //证件类型
                    identityType: '',
                    //身份证号码
                    idCard: '',
                    //审核状态
                    identityValidate: '',
                    photoidValidate: '',
                    videoValidate: '',
                    //固定状态
                    identityValidateState: '',
                    photoidValidateState: '',
                    videoValidateState: '',
                    //备注
                    checkRemark: '',
                    identityRemark: '',
                    photoidRemark: '',
                    videoRemark: '',
                },
                //查看身份信息双向绑定数据
                temp: {
                    //uid
                    userId: '',
                    //真实姓名
                    name: '',
                    //证件地区
                    identityArea: '',
                    //证件类型
                    identityType: '',
                    //身份证号码
                    idCard: '',
                    //备注
                    checkRemark: '',
                    //审核值
                    identityValidate: '',
                    //固定审核状态
                    identityValidateState: '',
                    photoidValidateState: '',
                    videoValidateState: '',

                    addOrupdata: 1
                },
                resetOrUpdateData: {
                    userId: '',
                    level: '',
                    referrerNum: '',
                    type: ''
                },
                //查看身份证信息的左边宽度
                formLabelWidth: '120px',
                //点击获取选择的值
                currentRow: null,
                //查询条件对象
                listQuery: {
                    nameUidNum: '',
                    accountStatus: '',
                    identityValidate: '',
                    pageSize: 15,
                    page: 1
                },
                //单个查询
                selectUserId: {
                    userId: ''
                },
                //实名审核筛选条件
                importanceOptions,
                //启用禁用筛选条件
                calendarTypeOptions,
                msg: '',
                criteriaQuery: {
                    nameUidNum: '',
                    identityValidate: '',
                    accountStatus: '',
                    userId: '',
                    pageSize: '',
                    pageNum: 15
                },
                tableHeader: [
                    {
                        label: "注册类型",
                        prop: "loginType",
                        width: 80,
                        align: 'center',
                        formatter(row, column, value) {
                            let item = DCTP.utils.findDataByValue(logintypeOptions, value, 'key');
                            return item ? item.display_name : '';
                        }
                    }, {
                        label: "推荐人UID",
                        prop: "referrerId",
                        width: 65,
                        align: 'center'
                    }, {
                        label: "会员UID",
                        prop: "userId",
                        width: 65,
                        align: 'center'
                    }, {
                        label: "手机号码",
                        prop: "phoneNum",
                        width: 90
                    }, {
                        label: "邮箱",
                        prop: "email",
                        width: 140
                    }, {
                        label: "国籍",
                        // prop: "cndict",
                        minWidth: 60,
                        formatter(row, column, value) {
                            return row.cndict === null ? "未提交" : row.cndict + "(" + row.endict + ")";
                        }
                    }, {
                        label: "姓名",
                        prop: "name",
                        minWidth: 80,
                    }, {
                        label: "证件类型",
                        prop: "identityType",
                        minWidth: 55,
                        align: 'center',
                        formatter(row, column, value) {
                            return value === 0 ? '身份证' : (value === 2 ? '护照' : '未知');
                        }
                    }, {
                        label: "证件号码",
                        prop: "idCard",
                        minWidth: 130,
                        align: 'center',
                    }, {
                        label: "实名认证",
                        prop: "identityValidate",
                        width: 110,
                        align: 'center',
                        formatter(row, column, value) {
                            return row.videoValidate == 3 ? "C3认证" : (row.photoidValidate == 3 ? "C2认证" : (row.identityValidate == 3 ? "C1认证" : "未认证"))
                        }
                    }, {
                        label: "等级",
                        prop: "memberRank",
                        width: 60,
                        align: 'center'
                    }
                    // , {
                    //     label: "状态",
                    //     prop: "accountStatus",
                    //     width: 80,
                    //     align: 'center'
                    //     // ,formatter(row, column, value) {
                    //     //     return value === 1 ? '正常' : (value === 2 ? '禁用' : '未知');
                    //     // }
                    // },
                ],
                tableHeader2: [
                    {
                        label: "推广人数",
                        prop: "referrerNum",
                        align: 'center',
                        width: 80,
                        formatter(row, column, value) {
                            return value == 0 || value == null ? "无" : value + '个';
                        }
                    }, {
                        label: "注册时间",
                        prop: "createTime",
                        width: 170,
                        formatter(row, column, value) {
                            return DCTP.utils.parseDateTimeStr(value)
                        }
                    }, {
                        label: "最后更新时间",
                        prop: "updateTime",
                        width: 170,
                        formatter(row, column, value) {
                            return DCTP.utils.parseDateTimeStr(value)
                        }
                    }
                ]
            }

        }
        ,
        computed: {
            tableDataLength() {
                return this.tableData.length;
            }
        }
        ,
        methods: {

            getList() {
                this.listLoading = true;
                fetchList(this.listQuery).then(response => {
                    this.tableData = response.data.userList;
                    this.total = response.data.total;
                    this.listLoading = false;
                    this.currentRow = null;
                })
            },
            /**
             * 条件筛选查询对象
             * @returns {Promise}
             */
            handleFilter() {
                this.getList();
            },
            //获取当前页数
            userPage(val) {
                this.listQuery.page = val;
                this.getList();
            },
            //获取每页多少条
            userPageSize(val) {
                this.listQuery.pageSize = val;
                this.getList();
            },
            /**
             * 修改身份信息方法
             */
            updateUser() {
                if (this.userbase.identityValidate === 4) {
                    this.userbase.checkRemark = this.userbase.identityRemark;
                } else if (this.userbase.photoidValidate === 4) {
                    this.userbase.checkRemark = this.userbase.photoidRemark;
                } else if (this.userbase.videoValidate === 4) {
                    this.userbase.checkRemark = this.userbase.videoRemark;
                } else {
                    this.userbase.checkRemark = '';
                }
                alterUser(this.userbase).then(response => {
                    if (response.result) {
                        this.dialogFormVisible = false;
                        this.msg = "操作成功!";
                        this.$message({
                            type: 'success',
                            message: this.msg
                        });
                        this.currentRow = null;
                        this.getList();
                    } else {
                        this.msg = response.msg;
                        this.$message({
                            type: 'error',
                            message: this.msg
                        });
                    }

                })
            },
            /**
             *获取
             *启用禁用账号
             */
            startUser() {
                disableUser(this.criteriaQuery).then(response => {
                    if (response.result) {
                        this.msg = "操作成功!";
                        this.$message({
                            type: 'success',
                            message: this.msg
                        });
                        this.currentRow = null;
                        this.getList();
                    } else {
                        this.msg = response.msg;
                        this.$message({
                            type: 'error',
                            message: this.msg
                        });
                        this.getList();
                    }

                });
            }
            ,
            /**
             *启用禁用的方法
             * 传入启用  禁用标识和'启用'还是'禁用'的string
             */
            enableDisable(int, str) {
                if (!this.currentRow) {
                    this.getMessage('请选择用户', false);
                    return;
                }
                let userId = this.currentRow.userId;
                this.criteriaQuery.userId = userId;
                this.criteriaQuery.accountStatus = int;
                this.$confirm('要' + str + '此用户账号,是否继续', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.startUser();
                }).catch(() => {

                });
            },

            //单个审核会员身份认证
            autonymCheckUser() {
                verifiedUser(this.criteriaQuery).then(response => {
                    if (response.result) {
                        this.msg = "操作成功!";
                        this.$message({
                            type: 'success',
                            message: this.msg
                        });
                        this.getList();
                    } else {
                        this.msg = response.msg;
                        this.$message({
                            type: 'success',
                            message: this.msg
                        });
                        this.getList();
                    }

                });
            },
            autonymCheck(userId) {
                this.criteriaQuery.userId = userId;
                this.criteriaQuery.identityValidate = 3;
                this.$confirm('是否审核通过此用户账号,是否继续', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.autonymCheckUser();
                }).catch(() => {

                });
            }
            ,


            handleChange(val) {
                if (val === 1) {
                    this.temp.identityType = 0;
                } else {
                    this.temp.identityType = 2;
                }
                if (val === 1) {
                    this.identityType = [{id: 0, name: '身份证'}];
                } else {
                    this.identityType = [{id: 2, name: '护照'}];
                }
            },
            validate() {
                if (!this.temp.name) {
                    this.$message.warning('请输入姓名');
                    return false;
                } else if (!this.temp.idCard) {
                    this.$message.warning('请输入身份证号码');
                    return false;
                }
                return true;
            },
            handleEdit() {
                console.log(this.userbase)
                //判断状态是否要给予改变
                if (this.userbase.identityValidate === 1) {
                    this.userbase.identityValidate = 3
                }
                if (this.userbase.photoidValidate === 1) {
                    this.userbase.photoidValidate = 3
                }
                if (this.userbase.videoValidate === 1) {
                    this.userbase.videoValidate = 3
                }
                this.userbase = this.temp;
                //判断是否要赋值备注
                if (this.userbase.identityValidate === 4) {
                    this.userbase.identityRemark = this.temp.checkRemark
                }
                if (this.userbase.photoidValidate === 4) {
                    this.userbase.photoidRemark = this.temp.checkRemark
                }
                if (this.userbase.videoValidate === 4) {
                    this.userbase.videoRemark = this.temp.checkRemark
                }
                this.userDetails = false;
                this.dialogFormVisible = true;
            }
            ,
            handleDeleteEdit() {
                if (!this.currentRow) {
                    this.getMessage('请选择用户', false);
                    return;
                }
                console.log( 1 )
                let userId = this.currentRow.userId;
                this.userDetails = true;
                this.listLoad = true;
                this.selectUserId.userId = userId;
                selectUser(this.selectUserId).then(response => {
                    if( !response.data ) return this.$message({ type: 'warning', message: '无效数据' })
                    this.temp = response.data;
                    this.temp.identityValidateState = response.data.identityValidate;
                    this.temp.photoidValidateState = response.data.photoidValidate;
                    this.temp.videoValidateState = response.data.videoValidate;
                    this.listLoad = false;
                    if (response.result) {
                        this.currentRow = null;
                    }
                });
            },
            getCityList() {
                getCity().then(result => {
                    this.citybos = result.data.citybos;
                })
            },
            // 重置和更新 type 类型 1:重置交易密码 2：重置google 3.重置手机号码 4：重置邮箱 5:修改推荐人 6：修改等级
            resetOrUpdate(type, uid, input) {
                this.resetOrUpdateData.type = type;
                if (type === 5) {

                } else if (type === 6) {

                } else {
                    this.resetOrUpdateData.userId = uid;
                }
                resetUpdate(this.resetOrUpdateData).then(response => {
                    if (response.result) {
                        this.getList();
                        this.modifyReferrerVisible = false;
                        this.modifyLevelVisible = false;
                        this.resetOrUpdateData.level = '';
                        this.resetOrUpdateData.type = '';
                        this.resetOrUpdateData.userId = '';
                        this.resetOrUpdateData.referrerNum = '';
                        this.currentRow = null;
                    }
                    this.getMessage(response.msg, response.result);
                });
            },
            // 操作 type 类型 1:重置交易密码 2：重置google 3.重置手机号码 4：重置邮箱 5:修改推荐人 6：修改等级 7：解锁登录密码
            operate(type, str) {
                if (!this.currentRow) {
                    this.getMessage('请选择用户', false);
                    return;
                }
                let uid = this.currentRow.userId;
                if (type >= 1 && type <= 4) {
                    this.$confirm(str + '？', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then(() => {
                        this.resetOrUpdate(type, uid, '');
                    }).catch(() => {

                    });
                } else if (type == 7) {
                    this.$confirm(str + '？', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then(() => {
                        unlockLoginPwd(uid).then(response => {
                            if (response.result) {
                                this.getList();
                                this.currentRow = null;
                            }
                            this.getMessage(response.msg, response.result);
                        });
                    }).catch(() => {

                    });
                } else {
                    switch (type) {
                        case 5:
                            this.modifyReferrerVisible = true;
                            this.resetOrUpdateData.userId = uid;
                            break;
                        case 6:
                            this.modifyLevelVisible = true;
                            this.resetOrUpdateData.userId = uid;
                            break;
                    }
                }
            },
            handleCurrentChange(val) {
                console.log(val);
                this.currentRow = val;
            }
        },
        created() {
            this.getList();
            this.getCityList();
        }
    }
</script>
<style>
    .el-dialog--small {
        width: 30%;
    }

    .images {
        width: 200px;
        height: 150px;
    }

    .colorRed {
        color: red;
    }

    .colorBlue {
        color: blue;
    }

    .colorGreen {
        color: green;
    }

</style>



