<template>
    <el-pagination @size-change="userPageSize" @current-change="userPage"
                   :current-page="listQuery.page"
                   :page-sizes="pagenums" :page-size="listQuery.pageSize"
                   layout="total, sizes, prev, pager, next, jumper" :total="total">
    </el-pagination>
</template>

<script>
    import DCTP from '@/utils/dctp';

    export default {
        props: {
            total: {
                type: Number,
                default: 0
            },
            userPageSize: {},
            userPage: {},
            listQuery: {
                type: Object,
                default: {
                    page: 0,
                    pageSize: 15,
                }
            }
        },
        data() {
            return {
                pagenums: DCTP.pagenums,
            }
        }
    }

</script>


